.app-home {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main-img {
  max-width: 30%;
  margin-top: 40px;
}

.home-h1 {
  font-size: 4rem;
}

.home-h2 {
  font-size: 1.5rem;
  margin: 20px;
}

.steps {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 80px;
  margin-bottom: 180px;
}

.step {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.step > img {
  width: 300px;
}

.dots {
  display: flex;
  flex-direction: column;
  margin: 20px;
}

.dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin: 10px;
}
