.call-container {
  position: fixed;
  left: 50%;
  bottom: -120%;
  transform: translateX(-50%) translateY(-50%);
  padding: 15px;
  background: #000;
  color: #fff;
  transition: 0.4s ease-in;
  height: 70%;
  min-width: 50%;
}

.show-mini {
  bottom: -20%;
}

#localVideo {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 200px;
}

#partnerVideo {
  max-height: 500px;
  overflow: hidden;
}

.call-buttons {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 30px;

  display: flex;
  flex-direction: row;
  justify-content: center;
}

.call-btn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #fff;
  color: #000;

  display: flex;
  justify-content: center;
  align-items: center;

  margin: 0 5px;
  cursor: pointer;
  color: #fff;
}

.incoming-call {
  color: #fff;

  padding: 30px;
  border-radius: 10px;
  position: fixed;
  top: 50%;
  left: 50%;
  background-color: #000;
  transform: translateX(-50%) translateY(-50%);

  transition: 0.3s ease-out;
}

.incoming-user-img {
  background-color: rgb(0, 0, 0);
  width: 200px;
  height: 200px;
  background-size: cover;
  border-radius: 50%;

  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid rgb(43, 43, 43);

  text-transform: uppercase;
  font-size: 6rem;
  font-weight: bold;
  letter-spacing: 3px;
}

.incoming-user-name {
  text-align: center;
  font-size: 1.5rem;
  margin-top: 10px;
}

.incoming-call-buttons {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding-top: 20px;
}

.incoming-show {
  top: 50%;
}
.incoming-hide {
  top: 150%;
}
