.main-search-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px;
  width: 80%;
  margin: 0 auto;
}

.search-bar {
  width: 60%;
}

.search-bar input {
  width: 100%;
}

.search-bar h1 {
  text-align: center;
}

.search-results {
  width: 60%;
}

.search-field {
  font-size: 3rem;
  font-weight: bold;
  padding: 20px 60px;
  border: none;
  outline: none;
  border-radius: 10px;
  margin-top: 10px;
}

.search-field-dark {
  background-color: #171d2d;
  box-shadow: 2px 2px 5px #000;
  color: #eee;
}

.search-field-light {
  background-color: #fff;
  box-shadow: 2px 2px 5px #ccc;
}

.user-preview-container {
  display: flex;
  align-items: center;
}

.user-info {
  padding-left: 20px;
}

.user-preview-container h3 {
  font-size: 1.5rem;
}

.user-preview-container h4 {
  font-weight: normal;
  max-width: 80%;
}

.user-image-wrap {
  min-width: 100px;
  height: 100px;
  overflow: hidden;
  border-radius: 50%;

  background-size: cover;
  background-position: center;
}
