.team-main-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.team-img {
  width: 300px;
  height: 300px;
  background-size: cover;
  border-radius: 50%;
  margin: 30px;
}

.team-name {
  font-size: 4rem;
}

.team-description {
  max-width: 500px;
  text-align: center;
}

.team-teammates {
  margin: 10px;
}

.team-teammates span {
  margin-left: 10px;
}

.techonologies ul {
  display: flex;
  margin-bottom: 20px;
}


.techonologies ul li {
  display: flex;
  color: #fff;
  background-color: #673AB7;
  padding: 10px;
  border-radius: 10px;
  margin: 0 5px;
}
